.social_content{
      width: 4rem;
      height: auto;
      flex-direction: column;
      gap: 1rem;
      position: relative;
      left: 0;
      top: 8rem;
      transform: translateX(-50%);
}

.btn_social{
      width: 4rem;
      height: 4rem;
      background: var(--primary);
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--text-100);
}

.btn_social *{
      font-size: 2rem;
}