.user_content{
      width: 53.4rem;
      height: 100%;
      margin-left: 6rem;
      margin-top: 7.5rem;
      gap: 2rem;
}

.user_avatar{
      width: 25rem;
      height: 25rem;
      position: relative;
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
}

.user_avatar__img{
      width: 24rem;
      height: 24rem;
      border-radius: 50%;
      position: relative;
      z-index: 2;
      overflow: hidden;
}

.user_infor{
      padding-top: 4rem;
}

.user_infor__title{
      position: relative;
      width: fit-content;
      height: fit-content;
}

.user_infor__title:before{
      width: 12rem;
      height: 2.4rem;
      background: #e6e6e6;
      right: 0;
      top: 1rem;
      transform: translateX(20%);
}

.user_infor__title h1{
      font-size: 5rem;
      color: var(--primary);
      line-height: 5rem;
      position: relative;
      text-transform: capitalize;
}

.user_infor strong{
      font-size: 2rem;
}

.user_location{
      margin-top: 1rem;
      align-items: first baseline;
}

.user_location *{
      font-size: 2.45rem;
      color: var(--primary);
}

.user_location span{
      font-size: 2.2rem;
      color: var(--text-300);
      text-transform: capitalize;
}