.main_page{
      width: 118rem;
      height: auto;
      min-height: calc(100vh - 8rem);
      background: var(--bg-200);
      margin: 8rem auto 0 auto;
      position: relative;
      z-index: 2;
}

.main_page:before{
      width: 82.3rem;
      height: 42.6rem;
      background: var(--bg-100);
      top: 6.3rem;
      right: 0;
}

.main_header{
      width: 100%;
      height: 25rem;
      position: relative;
      margin-top: 1.5rem;
      gap: 2rem;
}

.main_content{
      margin-top: 4rem;
      padding: 8rem;
      position: relative;
}

.content_section{
      gap: 6rem;
}

.column_section{
      width: 50%;
      height: auto;
}

.slider_friend{
      justify-content: center;
      margin-top: 8rem;
}

.section_gallery{
      margin-top: 10rem;
      justify-content: center;
}