.friends_content{
      width: 40rem;
      height: auto;
}

.friend_pad{
      width: 12rem;
      height: 12rem;
      padding: .5rem;
}

.friend_box{
      width: 11rem;
      height: 11rem;
      border-radius: 50%;
      overflow: hidden;
      align-items: center;
      justify-content: center;
}

.friend_box__img{
      width: 10rem;
      height: 10rem;
      position: relative;
      border-radius: 50%;
      overflow: hidden;
      background: var(--bg-200);
}

.friend_box__img a{
      position: absolute;
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      color: var(--text-100);
      background: rgba(0,0,0,.6);
      opacity: 0;
      transition: .2s ease-in-out;
}

.friend_box:hover .friend_box__img a{
      opacity: 1;
}

.friends_content .slick-arrow{
      display: none;
      opacity: 0;
      visibility: hidden;
}

.friends_content .slick-dots li button:before{
      color: var(--text-100);
      opacity: 1;
}

.friends_content .slick-dots li.slick-active button:before{
      color: var(--primary);
      opacity: 1;
}