.online_content{
      width: 100%;
      height: 4rem;
      gap: .5rem;
      align-items: center;
}

.online_avatar{
      width: 4rem;
      height: 4rem;
      position: relative;
      flex-shrink: 0;
}

.online_avatar__img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      overflow: hidden;
}

.online_dot{
      width: 1.2rem;
      height: 1.2rem;
      border-radius: 50%;
      position: absolute;
      bottom: 0;
      right: 0;
}

.online_dot.green{
      background: var(--green);
}

.online_dot.red{
      background: var(--red);
}

.online_content span{
      padding: 0 2rem;
      background: var(--bg-200);
      border-radius: 999px;
      color: var(--text-100);
      text-transform: uppercase;
      height: 2.4rem;
      font-size: 1rem;
      line-height: 2.4rem;
}