.gallery_content{
      width: 70rem;
      height: auto;
}

.gallery_pad{
      width: 23rem;
      height: 32rem;
      padding: .5rem;
}

.gallery_row{
      width: 100%;
      height: 100%;
      border-radius: 1rem;
      align-items: center;
      justify-content: center;
}

.gallery_row__img{
      width: calc(100% - .6rem);
      height: calc(100% - .6rem);
      border-radius: 1rem;
      overflow: hidden;
      position: relative;
      background: var(--bg-200);
}

button.slick-arrow{
      width: 4rem;
      height: 4rem;
      background: var(--bg-200);
      border-radius: 50%;
      z-index: 2;
      top: 16rem;
}

button.slick-arrow:hover{
      background: var(--bg-200);
}

button.slick-arrow.slick-next{
      right: -5.2rem;
}

button.slick-arrow.slick-prev{
      left: -5.2rem;
}

button.slick-arrow.slick-next:before,
button.slick-arrow.slick-prev:before{
      width: 1.4rem;
      height: 1.4rem;
      color: transparent;
      transform: rotate(45deg);
      top: 1.2rem;
      left: .8rem;
}

button.slick-arrow.slick-next:before{
      border-top: solid .5rem var(--primary);
      border-right: solid .5rem var(--primary);
}


button.slick-arrow.slick-prev:before{
      border-bottom: solid .5rem var(--primary);
      border-left: solid .5rem var(--primary);
      left: 1.4rem;
}