.background{
      position: fixed;
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
      background: var(--primary);
      overflow: hidden;
}

.background:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: linear-gradient(rgba(28,35,45,.7) 0%, var(--primary) 50%);
      z-index: 1;
}

.background img{
      filter: grayscale(1);
}