.relation_content{
      flex-grow: 1;
      max-width: 40rem;
      position: absolute;
      margin-top: 8rem;
      right: 0;
}

.online_group{
      width: 12.4rem;
      height: 4rem;
      position: absolute;
      top: 2rem;
      right: 2rem;
}

.relation_group{
      margin-top: 12rem;
      padding: 1rem;
      background: var(--bg-200);
      position: relative;
      gap: 1rem;
}

.relation_group:before{
      width: 100%;
      height: 100%;
      background: none;
      border: solid .1rem var(--primary);
      top: .5rem;
      left: -.5rem;
}

.relation_avatar,
.relation_avatar__img{
      width: 8rem;
      height: 8rem;
      overflow: hidden;
      flex-shrink: 0;
}

.relation_info{
      flex-grow: 1;
      position: relative;
      color: var(--text-100);
      max-height: 15rem;
      overflow: hidden;
}

.relation_info h2{
      color: var(--primary);
      font-size: 2rem;
}

.relation_info a{
      position: absolute;
      top: .5rem;
      right: 1rem;
      width: 4rem;
      height: 4rem;
      align-items: center;
      justify-content: center;
}

.relation_info a *{
      color: var(--primary);
      font-size: 2.6rem;
}

.relation_description{
      margin-top: 1rem;
}

.relation_description p{
      font-size: 1.2rem;
}