.user_about__content{
      flex-direction: column;
}

.about_title{
      width: 39rem;
      height: 3.6rem;
      background: var(--primary);
      align-items: center;
      justify-content: center;
      border-radius: 999px;
      position: relative;
}

.about_title:before{
      width: 100%;
      height: 100%;
      top: .5rem;
      left: .5rem;
      border: solid .1rem var(--text-100);
      border-radius: 999px;
}

.about_title h3{
      color: var(--text-100);
      font-weight: 400;
}

.about_description{
      width: 39rem;
      margin-top: 4rem;
}

.about_description p{
      color: var(--text-200);
}